<template>
  <div>
    <h1 class="title center-text">Settings</h1>
    <div class="settings-groups">
      <div class="journal-settings card">
        <span class="title">Journal Settings</span>
        <label for="journal-location"> Journal Location </label>
        <input class="input" id="journal-location" type="text" v-model="settings.journal_location">
        <button class="btn" @click="sendPathUpdate()">Save</button>
      </div>
      <div class="commanders card">
        <span class="title">Commander Settings</span>
        <span> current commander: {{ settings.current_commander }}</span>
        <span class="subtitle">Commanders</span>
        <span v-if="settings.commanders" class="commander-list">
          <span class="commander" @click="selectedCommander = commander.name" v-for="commander in settings.commanders"
                :key="commander.name">
            {{ commander.name }}
          </span>
        </span>
        <span>selected: {{ getSelectedCommander }}</span>
        <button class="btn" @click="sendSelectedCommander">Select Commander</button>
        <div class="new-commander">
          <span class="subtitle">Add commander</span>
          <input class="input" type="text" name="" id="name" v-model="newCommander.name" placeholder="Commander Name">
          <input class="input" type="text" name="" id="edsm_key" v-model="newCommander.edsm_key"
                 placeholder="EDSM Api Key">
          <input class="input" type="text" name="" id="edsm_name" v-model="newCommander.edsm_name"
                 placeholder="EDSM Name">
          <input class="input" type="text" name="" id="inara_key" v-model="newCommander.inara_key"
                 placeholder="Inara Api Key">
          <input class="input" type="text" name="" id="inara_name" v-model="newCommander.inara_name"
                 placeholder="Inara Name">
          <button class="btn" @click="sendNewCommander">Add commander</button>
        </div>
      </div>
    </div>
    <!--    <div>-->
    <!--      {{ getNewCommander.name }} <br>-->
    <!--      {{ getJournalPath }} <br>-->
    <!--    </div>-->
  </div>

</template>

<script>
import {api_get_call, api_post_call} from "@/api_calls";

export default {
  name: "Settings",
  data() {
    return {
      settings: {},
      selected_user: "",
      newCommander: {"name": "", "edsm_key": "", "edsm_name": "", "inara_key": "", "inara_name": ""},
      selectedCommander: ""
    }
  },
  computed: {
    getJournalPath() {
      return this.settings.journal_location
    },
    getNewCommander() {
      return this.newCommander
    },
    getSelectedCommander() {
      return this.selectedCommander
    }
  },
  methods: {
    async sendPathUpdate() {
      let body = {"journal_location": this.settings.journal_location}
      await api_post_call(body, "http://localhost:3500/api", "/set/settings/journal")
    },
    async sendNewCommander() {
      await api_post_call(this.getNewCommander, "http://localhost:3500/api", "/set/settings/newcommander")
      this.$router.go()
    },
    async sendSelectedCommander() {
      let body = {"commander": this.getSelectedCommander}
      await api_post_call(body , "http://localhost:3500/api", "/set/settings/currentcommander")
      this.$router.go()
    }
  }
  ,
  async mounted() {
    let data = await api_get_call("http://localhost:3500/api", "/get/settings")
    this.settings = data.results
  }
}
</script>

